@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #eaeaea;
  font-size: 14px;
  font-family: "Roboto", sans-serif; }

a {
  text-decoration: none;
  color: inherit; }

/* @define Header */
.Header {
  width: 100%;
  height: 5.71428571429em;
  background: #fff; }
  .Header > nav {
    display: grid;
    grid-template-columns: 30% 70%;
    width: 100%;
    align-items: center;
    height: 100%; }
    .Header > nav div .header-name {
      padding: 2em;
      display: block; }
    .Header > nav div:last-child {
      box-sizing: border-box;
      display: grid;
      justify-content: flex-end; }
      .Header > nav div:last-child ul {
        list-style: none;
        margin: 0;
        padding: 0; }
        .Header > nav div:last-child ul li {
          display: inline; }
          .Header > nav div:last-child ul li a {
            padding: 2em; }

/* @define Btn */
.Button {
  min-height: 38px;
  border: none;
  border-radius: 3px;
  min-width: 100px;
  font-size: 14px;
  cursor: pointer; }
  .Button span {
    margin-left: 10px; }

/* @define Home */
.Home {
  width: 100%;
  height: 100%; }
  .Home .content {
    background-image: url(/static/media/bg.6052945c.png);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    z-index: -1;
    display: grid;
    grid-template-columns: 50% 20% 30%;
    background-size: cover;
    background-position: center center; }
    .Home .content-left {
      padding: 3em 50px 0 50px;
      background-color: #0b83a7;
      position: relative;
      font-size: 2em;
      color: #fff;
      display: grid;
      justify-content: space-between;
      align-items: center; }
      .Home .content-left-tagline {
        max-width: 80%; }
      .Home .content-left-payment {
        font-size: 24px; }
        .Home .content-left-payment span {
          font-size: 18px;
          margin-bottom: 20px;
          display: block; }
        .Home .content-left-payment ul {
          list-style: none;
          margin: 0;
          padding: 0; }
      .Home .content-left-download {
        font-size: 18px; }
        .Home .content-left-download-button {
          display: grid;
          grid-row-gap: 10px;
          width: 200px;
          margin-top: 30px; }
    .Home .content-center::after {
      content: "";
      -webkit-transform: skewY(110deg);
              transform: skewY(110deg);
      -webkit-transform-origin: top left;
              transform-origin: top left;
      background: #0b83a7;
      position: absolute;
      width: 100%;
      top: 0;
      bottom: 0;
      z-index: 1;
      left: 50%; }
    .Home .content-center img {
      position: absolute;
      z-index: 3;
      width: 55%;
      top: 40%;
      left: 25%; }
    .Home .content-right {
      display: grid;
      justify-content: center;
      align-items: center; }
      .Home .content-right img {
        width: 70%;
        z-index: 4; }

